.bookmark-list .bookmark-item {
  -webkit-transition: all .15s ease-in;
  -o-transition: all .15s ease-in;
  transition: all .15s ease-in;
  padding: 1.5rem; }
  .bookmark-list .bookmark-item .site-name {
    font-size: 2rem;
    font-weight: 500;
    cursor: pointer; }
  .bookmark-list .bookmark-item .site-url {
    margin-top: 0.5rem;
    font-size: 1.5rem;
    cursor: pointer; }

.bookmark-list .bookmark-item + .bookmark-item {
  border-top: 1px solid #dee2e6; }



